import axios from "axios";
import { redirect } from "react-router-dom";
import { getAuth } from "./auth";

axios.defaults.baseURL = process.env.API_URL;

const postUtil = (url, data) => axios.post(url, data);

const putUtil = (url, data) =>
  axios({
    method: "put",
    url,
    data,
  });

const patchUtil = (url, data) =>
  axios({
    method: "patch",
    url,
    data,
  });

const getUtil = (url, data = null) => axios.get(url, { params: data });

const deleteUtil = (url, query, data = null) =>
  axios.delete(url, { params: query, data });

export function setDefault() {
  const authourityToken = getAuth();
  if (authourityToken) {
    const headers = {
      Authorization: `Bearer ${authourityToken}`,
      "Access-Control-Allow-Origin": "*",
    };
    axios.defaults.headers = headers;
  }
}

export { postUtil, getUtil, putUtil, patchUtil, deleteUtil };

axios.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response.status === 400) {
      return error.response;
    }
    if (error.response.status === 401) {
      localStorage.clear();
      redirect("/login");
    }
    if (
      error.response.status === 400 ||
      error.response.status === 404 ||
      error.response.status === 500
    ) {
      return error;
    }
    return error;
  }
);
