import React from "react";
import { useQuery } from "@tanstack/react-query";
import { getUsers } from "../shared/apis";

const Home = () => {
  const { isLoading, data, isError } = useQuery({
    queryKey: ["users"],
    queryFn: getUsers,
  });

  const { data: users = [] } = data || {};

  return (
    <div>
      <h2>Home</h2>
      {users?.map(({ name }) => (
        <h4>{name}</h4>
      ))}
    </div>
  );
};

export default Home;
